@import "./../../styles/constants.scss";

.header {
  width: 100%;
  height: $header-height;
  border-bottom: 1px solid $base-font-border-color;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  margin: 0 auto;

  @media (min-width: $breakpoint-mid) {
    width: 90%;
  }
}

.header__logo {
  background-image: url("../../images/EkoOknaLogo.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100px;
  cursor: pointer;
}

.header__list {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}

.header__list-link {
  text-decoration: none;
  transition: 0.5s;
  border-bottom: 1px solid transparent;

  @media (min-width: $breakpoint-mid) {
    &:hover {
      color: $font-border-hover-color;
      border-bottom: 1px solid $font-border-hover-color;
    }
  }
}

.header__list-item {
  margin-left: 10px;
  padding: 5px 10px;
  text-transform: uppercase;
  cursor: pointer;
}
